body {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Arial, sans-serif;
  font-size: 30px;
}

.calendar {
  max-width: 707px;
  display: flex;
  flex-wrap: wrap;
  gap: 1px;
}

.calendar--month-28 :nth-child(n+29) {
  display: none;
}

.calendar--month-29 :nth-child(n+30) {
  display: none;
}

.calendar--month-30 :nth-child(n+31) {
  display: none;
}

.calendar--month-31 :nth-child(n+32) {
  display: none;
}

.calendar--start-tue :first-child {
  margin-left: 101px;
}

.calendar--start-wed :first-child {
  margin-left: 202px;
}

.calendar--start-thu :first-child {
  margin-left: 303px;
}

.calendar--start-fri :first-child {
  margin-left: 404px;
}

.calendar--start-sat :first-child {
  margin-left: 505px;
}

.calendar--start-sun :first-child {
  margin-left: 606px;
}

.calendar__day {
  width: 100px;
  height: 100px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  background-color: #eee;
  position: relative;
  transition: background-color 0.5s, transform 0.5s;
}

